* {
  transition: background .3s, color .3s, filter .3s; }

a {
  color: inherit;
  text-decoration: none; }
  a:focus {
    outline: none;
    border-color: transparent; }

h1, figure, fieldset {
  margin: 0;
  padding: 0; }

input, select, textarea, option {
  appearance: none;
  -ms-appearance: none;
  border-radius: 0;
  border: 0;
  padding: 0; }
  input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active, option:focus, option:active {
    outline: 0; }

label {
  cursor: pointer;
  user-select: none; }

fieldset {
  border: none; }

body {
  overflow-x: hidden;
  height: 100%; }

button {
  cursor: pointer;
  font-family: inherit;
  color: inherit;
  appearance: none;
  border: 0;
  padding: 0;
  background: transparent;
  letter-spacing: 0.4px;
  line-height: 1;
  user-select: none; }
  button:focus, button:active {
    appearance: none;
    outline: none; }

h1 {
  margin: 0;
  color: lightseagreen; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

p {
  margin: 0; }

*::selection {
  color: #7a3a3a;
  background: #d7bc9f; }

header {
  padding: 10px; }

.wrapper {
  max-width: 1024px;
  margin: 0 auto; }

.btn {
  text-decoration: none;
  display: block; }

.post-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0 -15px; }

.post-item {
  margin: 15px;
  background: #f2e0d2;
  border-radius: 5px;
  padding: 15px;
  display: grid !important;
  grid-template-rows: 1fr 130px 1fr; }

.post-name {
  color: #7a3a3a;
  text-transform: uppercase; }

.post-message {
  margin: 10px 0;
  overflow: auto; }

.post-message p {
  color: #3d362e; }

.post-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: end; }

.post-content .user {
  color: #a8a8a6; }

.btn-comment {
  padding: 5px;
  background: #d7bc9f;
  color: #3d362e;
  border-radius: 5px; }

.btn-comment:hover {
  background: #caab8b; }

.comments-list {
  display: none; }

.comment-item {
  width: 100%;
  padding: 2.5px 0; }

.btn-open-comment {
  box-sizing: border-box;
  width: 100%;
  background: #f3e9e1;
  color: #333333;
  padding: 2.5px 5px; }

.btn-open-comment:hover {
  color: inherit; }

.comments-mode .post-body {
  display: none; }

.comments-mode .comments-list {
  display: block; }

.holder {
  text-align: center;
  padding: 10px 0; }

.holder a {
  padding: 5px 10px;
  border-radius: 5px;
  margin: 0 5px;
  text-decoration: none;
  color: #2fa09a;
  background: #ffffff;
  border: 1px solid #2fa09a;
  display: inline-block;
  min-width: 18px; }

.holder a:not(.jp-current):hover {
  background: #2fa09a;
  color: #ffffff;
  cursor: pointer; }

.holder a.jp-current:hover {
  cursor: default; }

.holder a.jp-current {
  color: #ffffff;
  background: #278682;
  border: 1px solid #278682; }
